import uniLogin from '@/components/uni-login';
export default {
	components: {
	    uniLogin
	},
	data() {
		return {
			maskState: 0, //优惠券面板显示状态
			desc: '', //备注
			payType: 1, //1微信 2支付宝
			amount:1,
			isLive:0, //是否来自直播 0=非直播
			sid:0,
			goods:{},
			specSelected:{
				size:'',
				color:''
			},
			couponList: [
				{
					title: '新用户专享优惠券',
					price: 5,
				},
			],
			addressData: {},
			order_id:0,
			userInfo:{},
			share_uid:0
		}
	},
	onShow() {
		this.userInfo = uni.getStorageSync('userInfo')
		// if(!uni.getStorageSync('session_key') || !this.userInfo.id){
		// 	uni.navigateTo({
		// 		url : '/pages/public/login'
		// 	})  
		// 	return;
		// }
	},
	onLoad(option){
		//商品数据
		let data = JSON.parse(option.detail);
		this.specSelected = {
			size:data.size,
			color:data.color
		}
		
		this.isLive = data.live;
		this.sid = data.sid;
		this.share_uid = data.share_uid;
		this.userInfo = uni.getStorageSync('userInfo')
		if(!uni.getStorageSync('session_key') || !this.userInfo.id){
			// uni.navigateTo({
			// 	url : '/pages/public/login'
			// })  
			return;
		}
		
		this.initData(data);
		
	},
	methods: {
		initData(option){
			
			uni.showLoading({
				title: "加载中..."
			});		
			uni.request({
				url: this.$Url + '/clmsj/Address/get_default_address',
				data: {},
				header:{	'auth' : uni.getStorageSync('session_key')	},
				success: (res) => {
					
					if(res.data.code ==0){
						this.addressData = res.data.data.address;
					}else{
						uni.hideLoading();
						uni.showToast({
							icon:'none',
							title:'请先设置收货信息再进行购买'
						});
						// setTimeout(()=>{
						// 	uni.navigateTo({
						// 		url:'/pages/address/address'
						// 	})
						// },1500);
						
					}
				}
			});
			uni.request({
				url: this.$Url + '/clmsj/goods/goodsdetail_shop',
				data: {
					goods_id : option.goods_id,
					shop_id : option.sid
				},
				header:{	'auth' : uni.getStorageSync('session_key')	},
				success: (res) => {
					console.log(res.data.data);
					this.goods = res.data.data;
				}
			});
			uni.hideLoading();
		},
		//显示优惠券面板
		toggleMask(type){
			let timer = type === 'show' ? 10 : 300;
			let	state = type === 'show' ? 1 : 0;
			this.maskState = 2;
			setTimeout(()=>{
				this.maskState = state;
			}, timer)
		},
		numberChange(data) {
			this.number = data.number;
		},
		changePayType(type){
			this.payType = type;
		},
		submit(){
			if(this.order_id == 0){
				if(!this.addressData.id){
					uni.showToast({
						icon:'none',
						title:'请先设置您的收货地址'
					})
					return false;
				}
				console.log("save order =share_uid : " + this.share_uid)
				uni.request({
					url: this.$Url + '/clmsj/order/saveOrder',
					data: {
						shop_id : this.sid,
						goods_id : this.goods.goods_id,
						live : this.isLive,
						desc : this.desc,
						address_id: this.addressData.id,
						specSelected : this.specSelected,
						share_uid : this.share_uid
					},
					header:{ 'auth' : uni.getStorageSync('session_key') },
					success: (res) => {
						console.log('创建订单成功')
						this.order_id = res.data.data;
						uni.navigateTo({
							url:'/pages/money/pay?order_id=' + this.order_id
						})
						// this.payment(this.order_id);
					}
				});
			}else{
				uni.navigateTo({
					url:'/pages/money/pay?order_id=' + this.order_id
				})
				// this.payment(this.order_id);
			}
			// uni.redirectTo({
			// 	url: '/pages/money/pay'
			// })
		},
		payment(order_id){
			uni.request({
				url: this.$Url + '/clmsj/order/getPayParam',
				data: { order_id: order_id },
				header:{ 'auth' : uni.getStorageSync('session_key') },
				success: (resp) => {
					let res = resp.data.data;
					// 调起支付
					uni.requestPayment({
						provider: 'wxpay',
						timeStamp: res.timeStamp,
						nonceStr: res.nonceStr,
						package: res.package,
						signType: 'MD5',
						paySign: res.paySign,
						success: (res) => {
							uni.showToast({
								title:'支付成功'
							});
							this.order_id = '';
							uni.redirectTo({
								url: '/pages/money/paySuccess?order_id='+this.order_id
							})
						},
						fail: (err)=> {
							console.log('fail:' + JSON.stringify(err));
							uni.showToast({
								icon:'none',
								title:'支付失败'
							})
						}
					});
				}
			});
		},
		stopPrevent(){}
	}
}
